import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import VueRouter from 'vue-router'
import router from './router'
import store from './store'
import './assets/tailwind.css'
import axios from 'axios'

//global variable
Vue.prototype.$axios = axios
// Vue.prototype.$strapi_url = "http://localhost:1337/api/"
Vue.prototype.$strapi_url =
    process.env.NODE_ENV === 'production' ? 'https://demo-cms.lotusflare.com/api/' : 'http://localhost:1337/api/'
// Vue.prototype.$upload_url = "http://localhost:1337"
Vue.prototype.$upload_url =
    process.env.NODE_ENV === 'production' ? 'https://demo-cms.lotusflare.com/' : 'http://localhost:1337'

Vue.config.productionTip = false
Vue.use(VueRouter)

new Vue({
    vuetify,
    render: (h) => h(App),
    router,
    store,
}).$mount('#app')
