// import { getOffers } from '@/api'
import { getOffersStrapi, getCategoriesStrapi, importOffersStrapi } from '@/api'

const state = {
    offers: {},
    parentCategories: {},
    subCategories: {},
    filteredOffers: {},
    currentParentCategory: null,
    loading: false,
    drawer: false,
    sort: null,
}
const mutations = {
    SET_OFFERS(state, offers) {
        state.offers = offers
        // console.log('SET_OFFERS', state.offers)
    },
    SET_PARENT_CATEGORIES(state, parentCategories) {
        state.parentCategories = parentCategories
        // console.log('SET_PARENT_CATEGORIES', state.parentCategories)
    },
    SET_SUB_CATEGORIES(state, subCategories) {
        state.subCategories = subCategories
        // console.log('SET_SUB_CATEGORIES', state.subCategories)
    },
    SET_FILTERED_OFFERS(state, filteredOffers) {
        state.filteredOffers = filteredOffers
        console.log('SET_FILTERED_OFFERS', state.filteredOffers)
    },
    SET_DRAWER(state, drawer) {
        state.drawer = drawer
    },
    SET_CURRENT_PARENT_CATEGORY(state, currentParentCategory) {
        state.currentParentCategory = currentParentCategory
    },
    SET_SORT(state, sort) {
        state.sort = sort
    },
}
const actions = {
    async importOffers() {
        const result = await importOffersStrapi()
        console.log('importOffersStrapi', result)
    },
    async getOffers({ commit }) {
        state.loading = true
        const offers = await getOffersStrapi(null, null)
        state.loading = false
        commit('SET_OFFERS', offers.data)
    },
    async getParentCategories({ commit }) {
        const parentCategories = await getCategoriesStrapi('parent', null)
        const parentCategoriesName = parentCategories.data.map((item) => item.attributes.Name)
        commit('SET_PARENT_CATEGORIES', parentCategoriesName)
    },
    async getSubCategories({ commit }, parentCategoryName) {
        const subCategories = await getCategoriesStrapi('children', parentCategoryName)
        const subCategoriesName = subCategories.data.map((item) => item.attributes.Name)
        commit('SET_SUB_CATEGORIES', subCategoriesName)
    },

    async getFilteredOffers({ commit }, categories) {
        console.log('@@@getFilteredOffers', categories)
        // if categories include 'other', then get the parent category's offers
        if (categories.includes('Other')) {
            let newCategories = []
            for (let i = 0; i < categories.length; i++) {
                if (categories[i] !== 'Other') {
                    newCategories.push(categories[i])
                }
            }
            newCategories.push(state.currentParentCategory)
            const offers = await getOffersStrapi(newCategories, state.sort)
            commit('SET_FILTERED_OFFERS', offers.data)
        } else if (categories === 'Uncategorized') {
            const offers = await getOffersStrapi([categories], state.sort)
            commit('SET_FILTERED_OFFERS', offers.data)
        } else if (categories.length === 0) {
            let newCategories = []
            for (let i = 0; i < state.subCategories.length; i++) {
                if (state.subCategories[i] !== 'Uncategorized') {
                    newCategories.push(state.subCategories[i])
                }
            }
            newCategories.push(state.currentParentCategory)
            const offers = await getOffersStrapi(newCategories, state.sort)
            commit('SET_FILTERED_OFFERS', offers.data)
        } else {
            const offers = await getOffersStrapi(categories, state.sort)
            commit('SET_FILTERED_OFFERS', offers.data)
        }
    },
    getCurrentParentCategory({ commit }, parentCategory) {
        commit('SET_CURRENT_PARENT_CATEGORY', parentCategory)
    },
    // drawer
    toggleDrawer({ commit }) {
        commit('SET_DRAWER', !state.drawer)
    },
    setSort({ commit }, sort) {
        commit('SET_SORT', sort)
    },
}
const getters = {}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
}
