// get offers from strapi by category names and sort by user input
export const getOffersStrapi = (categoryNames, sort) => {
    let url = `/stores?`
    if (categoryNames?.length > 0) {
        for (let i = 0; i < categoryNames.length; i++) {
            if (categoryNames[i] !== 'Uncategorized') {
                url = url + `filters[$or][${i}][categories][name][$eq]=${categoryNames[i]}&`
            } else {
                url = url + `filters[categories][name][$notNull]&`
            }
        }
    }
    if (sort) {
        url += `&sort[0]=${sort}`
    } else {
        url += `&sort[0]=title`
    }
    return requestsStrapi({
        url: url + `&populate=*`,
        method: 'get',
    })
}
/**
 *
 * @param {*} type // parent, children
 * @param {*} parent // parent name
 * @returns
 */
export const getCategoriesStrapi = (type, parent) => {
    let url = `/categories?&sort[0]=sortPriority&sort[1]=Name`
    if (type === 'parent') {
        url += `&filters[parent]`
    } else if (type === 'children') {
        url += `&filters[parent][Name][$eq]=${parent}`
    } else {
        console.log('type is not valid')
    }
    return requestsStrapi({
        url: url + `&populate=*`,
        method: 'get',
    })
}

export const importOffersStrapi = () => {
    return requestsStrapi({
        url: `/import`,
        method: 'get',
    })
}
