                        </template> -->
                        <!-- <template v-else> -->
                        <template v-for="menuitem of menulist">
                            <v-menu
                                offset-y
                                :close-on-content-click="true"
                                open-on-hover
                                rounded="0"
                                v-if="menuitem.title === 'Plans'"
                                v-bind:key="menuitem.id"
                            >
                                <template #activator="{ on, attrs }">
                                    <button
                                        text
                                        color="white"
                                        v-bind="attrs"
                                        v-on="on"
                                        class="
                                            capitalize
