                                <!-- <router-link style="text-decoration: none; color: inherit" to="/plans"> -->
                                <v-sheet
                                    @click="handleClick"
                                    @mouseup="handleClick"
                                >
                                    <v-list class="!p-0">
                                        <v-list-item
                                            v-for="(
                                                parentCategory, index
                                            ) in parentCategories"
                                            :key="index"
                                            class="
                                                bg-flareTelBg
                                                text-flareTelBlack
                                                hover:!text-flareTelBg
                                                hover:bg-flareTelBlack
                                                hover:cursor-pointer
                                            "
                                            :data-index="index"
                                        >
                                            <v-list-item-title
                                                :data-index="index"
                                            >
                                                <!-- <v-btn text color="primary"> -->
