<script>
import router from "@/router";
import { mapActions, mapState } from "vuex";
// import CartDrawer from "./CartDrawer.vue";
import CartDrawerStrapi from "@/components/CartDrawerStrapi.vue";
export default {
    name: "AppHeader",
    data() {
        return {
            configs: [],
            menulis: [],
            drawer: false,
            items: [
                { title: "Home", icon: "mdi-view-dashboard" },
                { title: "About", icon: "mdi-forum" },
            ],
            uncategorizedOffers: false,
            // cartItemCount: 0,
            // get count() {
            //     let count = 0;
            //     if (sessionStorage.getItem("cart")) {
            //         const cart = JSON.parse(sessionStorage.getItem("cart"));
            //         for (const key in cart) {
            //             count += cart[key].quantity;
            //         }
            //     }
            //     return count;
            // },
            menulist: null
        };
    },
    components: {
        // CartDrawer,
        CartDrawerStrapi,
    },
    methods: {
        ...mapActions("user", ["userLogout"]),
        ...mapActions("home", [
            "getParentCategories",
            "getSubCategories",
            "getFilteredOffers",
            "getOffers",
            "getCurrentParentCategory",
            "toggleDrawer",
        ]),
        isLoggedin() {
            return sessionStorage.getItem("api_token") !== null;
        },
        logout() {
            this.userLogout();
            // reload
            this.$router.go();
        },
        handleClick(e) {
            // index is the id of the parent category
            // const index = e.target.getAttribute("data-index");
            // parentCategory is the name of the parent category
            const parentCategory = e.target.innerText;
            // if this page is not plans page, redirect to plans page
            this.getCurrentParentCategory(parentCategory); // send parentCategory to store
            if (parentCategory === "Uncategorized") {
                this.getFilteredOffers(parentCategory);
            } else {
                this.getSubCategories(parentCategory);
            }
            if (this.$route.path !== "/plans") {
                router.push("/plans");
            }
        },
    },
    computed: {
        ...mapState({
            offers: (state) => state.home.offers,
            parentCategories: (state) => state.home.parentCategories,
            subCategories: (state) => state.home.subCategories,
        }),
    },
    watch: {},
    async mounted() {
        await this.$axios
            .get(this.$strapi_url + "navigation/render/1")
            .then((res) => {
                console.log(res.data);
                this.menulist = res.data;
            });
        await this.$axios
            .get(this.$strapi_url + "config?populate=*")
            .then((res) => {
                this.configs = res.data;
            });
        await this.getOffers();
        await this.getParentCategories();
        for (const key in this.offers.offer_by_id) {
            if (!this.offers.offer_by_id[key].data?.categories) {
                this.uncategorizedOffers = true;
                break;
            }
        }
    },
};
</script>

