<script>
import Decimal from "decimal.js";
// import FTCheckbox from "@/components/FTCheckbox.vue";
import { mapState, mapActions } from "vuex";
export default {
    name: "FTCheckoutPayment",
    props: {},
    components: {
        // FTCheckbox,
    },
    data() {
        return {
            cart: [],
            selected: {
                checked: [],
                disabled: [],
            },
            cardInfo: {
                cardNumber: "",
                cardHolder: "",
                cardExpireMonth: "",
                cardExpireYear: "",
                cardCVV: "",
            },
            SaveCardSwitch: false,
        };
    },
    methods: {
        ...mapActions("user", [
            "userLogin",
            "addAccount",
            "addAddress",
            "createOrder",
            "addSubscriber",
            "userLogout",
        ]),
        ...mapActions("checkout", [
            "setCard",
            "setAccountError",
            "setNumberError",
            "setStepper",
            "resetModel",
        ]),
        multiply(a, b) {
            return new Decimal(a).times(b).toNumber();
        },
        add(a, b) {
            return new Decimal(a).plus(b).toNumber();
        },
        removeItem(id) {
            this.cart = this.cart.filter((item) => item.data.id !== id);
            // update cart in session storage
            sessionStorage.setItem("cart", JSON.stringify(this.cart));
        },
        incrementQuantity(index) {
            this.cart[index].quantity += 1;
            // update cart in session storage
            sessionStorage.setItem("cart", JSON.stringify(this.cart));
        },
        decrementQuantity(index) {
            if (this.cart[index].quantity > 1) {
                this.cart[index].quantity -= 1;
                // update cart in session storage
                sessionStorage.setItem("cart", JSON.stringify(this.cart));
            }
        },
        async placeOrder() {
            // connect card expiry
            const cardExpire = `${this.cardInfo.cardExpireMonth}/${this.cardInfo.cardExpireYear}`;
            // set card info
            this.setCard({
                cardNumber: this.cardInfo.cardNumber,
                cardHolder: this.cardInfo.cardHolder,
                cardExpire: cardExpire,
                cardCVV: this.cardInfo.cardCVV,
            });
            this.loading = true;
            this.setAccountError(200);
            this.setNumberError(200);
            try {
                await this.addAddress({
                    addAddress_parts: {
                        address: this.addressInfo.street,
                        suite: this.addressInfo.suite,
                        city: this.addressInfo.city,
                        state: this.addressInfo.state,
                        zip: this.addressInfo.zip,
                    },
                    addAddress_type: 1,
                    addAddress_target_id: sessionStorage.getItem("account_id"),
                    // addAddress_target_id: this.accountID,
                    addAddress_target_type: 2,
                });

                for await (let item of this.cart) {
                    for (let i = 0; i < item.quantity; i++) {
                        await this.createOrder({
                            // with strapi
                            entity_id: item.data.attributes.portalid,
                            // without strapi
                            // entity_id: item.id,
                            entity_type: "offer",
                            createOrder_target_id:
                                sessionStorage.getItem("account_id"),
                            createOrder_target_type: 2,
                        });
                    }
                }
                // await this.addSubscriber({
                //     addSubscriber_target_id: sessionStorage.getItem('account_id'),
                //     // addSubscriber_target_id: this.accountID,
                //     addSubscriber_target_type: 2,
                //     subscriber_info: {
                //         msisdn: this.numberInfo.number,
                //     },
                // })
                await this.$router.push("/success");
                // clear cart
                // sessionStorage.removeItem('cart')
                sessionStorage.setItem("cart", JSON.stringify([]));
            } catch (e) {
                console.log(e);
                // if the error is caused by the user login or creact account, go to the creact account step
                if (
                    e.request.responseURL.includes("login") ||
                    e.request.responseURL.includes("account")
                ) {
                    // if error occured, logout user first
                    this.userLogout();
                    this.setAccountError(e.response.status);
                    // set step to account
                    this.setStepper(2);
                } else if (
                    e.request.responseURL.includes("/orderoe/create_order")
                ) {
                    // set step to address
                    if (e.response.status === 500) {
                        this.alert = true;
                        this.alertMessage = "Internal Server Error";
                    } else {
                        this.setNumberError(e.response.status);
                        this.setStepper(4);
                    }
                } else {
                    // other errors, show alert
                    this.alert = true;
                    this.alertMessage = e.message;
                }
                this.loading = false;
            } finally {
                this.loading = false;
            }
            // this.$router.push({ name: "RwSuccess" });
        },
    },
    computed: {
        ...mapState("checkout", {
            accountInfo: (state) => state.accountInfo,
            addressInfo: (state) => state.addressInfo,
        }),
        ...mapState("user", {
            userID: (state) => state.user_id,
            apiToken: (state) => state.api_token,
            accountID: (state) => state.account_id,
        }),
        subtotal() {
            let total = 0;
            this.cart.forEach((item) => {
                total = this.add(
                    this.multiply(item.data.attributes.price, item.quantity),
                    total
                );
            });
            return total;
        },
        payInFull() {
            let total = 0;
            this.cart.forEach((item) => {
                if (!item.afterpay) {
                    total = this.add(
                        this.multiply(
                            item.data.attributes.price,
                            item.quantity
                        ),
                        total
                    );
                } else {
                    total = this.add(0, total);
                }
            });
            return total;
        },
        afterpay() {
            let total = 0;
            this.cart.forEach((item) => {
                if (item.afterpay) {
                    total = this.add(
                        this.multiply(
                            item.data.attributes.price / 4,
                            item.quantity
                        ),
                        total
                    );
                } else {
                    total = this.add(0, total);
                }
            });
            return total;
        },
    },
    watch: {
        selected: {
            handler() {
                console.log(this.selected);
            },
            deep: true,
        },
    },
    mounted() {
        // get cart from session storage
        this.cart = JSON.parse(sessionStorage.getItem("cart"));
        // console.log the route
        // console.log(this.$route.fullPath);
        if (sessionStorage.getItem("api_token") === null) {
            this.$router.push({ name: "RwLogin" });
        }
    },
};
</script>