<script>
import { mapState, mapActions } from "vuex";
export default {
    name: "MyAccount",
    data() {
        return {
            loggedIn: false,
            getPaymentInfo: [], // save each payment info using localStorage
            paymentInfoRearranged: [], // save rearranged payment info
        };
    },
    methods: {
        ...mapActions("dashboard", ["getAccountsAndOrderInfo"]),
        rearrangePaymentInfo(accountID) {
            let paymentInfo = this.getPaymentInfo;
            for (let i = 0; i < paymentInfo.length; i++) {
                if (paymentInfo[i].accountId === accountID) {
                    this.paymentInfoRearranged.push(paymentInfo[i]);
                }
            }
            // return paymentInfoRearranged
        },

        // convert timestamp to MM/DD/YYYY hh:mm:ss format
        convertTimestamp(timestamp) {
            let date = new Date(timestamp * 1000);
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();
            let hour = date.getHours();
            let minute = date.getMinutes();
            let second = date.getSeconds();
            if (month < 10) {
                month = "0" + month;
            }
            if (day < 10) {
                day = "0" + day;
            }
            if (hour < 10) {
                hour = "0" + hour;
            }
            if (minute < 10) {
                minute = "0" + minute;
            }
            if (second < 10) {
                second = "0" + second;
            }
            return (
                month +
                "/" +
                day +
                "/" +
                year +
                " " +
                hour +
                ":" +
                minute +
                ":" +
                second
            );
        },
    },
    computed: {
        ...mapState("dashboard", {
            userId: (state) => state.userId,
            userEmail: (state) => state.userEmail,
            MSISDN: (state) => state.MSISDN,
            accountIDs: (state) => state.accountIDs,
            accountsInfo: (state) => state.accountsInfo,
            orders: (state) => state.orders,
        }),
    },
    async mounted() {
        if (sessionStorage.getItem("api_token")) {
            this.loggedIn = true;
            await this.getAccountsAndOrderInfo(
                sessionStorage.getItem("user_id")
            );
        } else {
            this.loggedIn = false;
        }
    },
};
</script>

