<template>
    <div>
        <div class="flex flex-col items-center h-screen gap-[28px]">
            <div
                class="box-border w-[456px] mt-24 p-8 bg-white"
                :class="
                    $route.name === 'RwRegisterEmail'
                        ? 'h-[358px]'
                        : 'h-[318px]'
                "
            >
                <div class="flex flex-col">
                    <div
                        class="
                            not-italic
                            font-sans font-semibold
                            text-xl
                            leading-7
                            text-black
                        "
                        v-if="this.$route.name === 'RwLoginEmail'"
                    >
                        Welcome Back
                    </div>
                    <div
                        class="
                            not-italic
                            font-sans font-semibold
                            text-xl
                            leading-7
                            text-black
                        "
                        v-if="this.$route.name === 'RwRegisterEmail'"
                    >
                        Create an Account
                    </div>
                    <div
                        class="
                            mt-2
                            not-italic
                            font-sans font-medium
                            text-base
                            leading-6
                            text-gray-500
                        "
                        v-if="this.$route.name === 'RwLoginEmail'"
                    >
                        Login to continue
                    </div>
                    <div
                        class="
                            mt-2
                            not-italic
                            font-sans font-medium
                            text-base
                            leading-6
                            text-gray-500
                        "
                        v-if="this.$route.name === 'RwRegisterEmail'"
                    >
                        Sign up to continue
                    </div>
                    <form class="flex flex-col mt-[14px]">
                        <div
                            v-if="$route.name === 'RwRegisterEmail'"
                            class="flex flex-row flex-none gap-2.5"
                        >
                            <div
                                class="
                                    border-solid border border-gray-500
                                    w-1/2
                                    h-10
                                "
                            >
                                <input
                                    type="text"
                                    class="w-3/4 h-9 ml-4 focus:outline-none"
                                    placeholder="First Name"
                                    v-model="accountInfo.firstName"
                                    name="firstName"
                                />
                            </div>
                            <div
                                class="
                                    border-solid border border-gray-500
                                    w-1/2
                                    h-10
                                "
                            >
                                <input
                                    type="text"
                                    class="w-11/12 h-9 ml-4 focus:outline-none"
                                    placeholder="Last Name"
                                    v-model="accountInfo.lastName"
                                    name="lastName"
                                />
                            </div>
                        </div>
                        <div
                            class="
                                mt-4
                                border-solid border border-gray-500
                                w-full
                                h-10
                            "
                        >
                            <input
                                type="text"
                                class="w-11/12 h-9 ml-4 focus:outline-none"
                                placeholder="Email"
                                v-model="accountInfo.email"
                            />
                        </div>
                        <div
                            class="
                                mt-4
                                border-solid border border-gray-500
                                w-full
                                h-10
                                flex flex-row
                            "
                        >
                            <!-- password input with eye icon-->
                            <input
                                :type="showPassword ? 'text' : 'password'"
                                class="
                                    w-11/12
                                    h-9
                                    ml-4
                                    focus:outline-none
                                    flex flex-row
                                "
                                placeholder="Password"
                                v-model="accountInfo.password"
                                name="password"
                            />
                            <!-- eye icon -->
                            <div>
                                <v-icon
                                    class="mt-2 mr-2"
                                    @click="showPassword = !showPassword"
                                    v-if="showPassword"
                                    >mdi-eye</v-icon
                                >
                                <v-icon
                                    class="mt-2 mr-2"
                                    @click="showPassword = !showPassword"
                                    v-else
                                    >mdi-eye-off</v-icon
                                >
                            </div>
                        </div>
                        <!-- submit button -->
                        <div
                            class="
                                mt-4
                                border-solid border border-gray-500
                                w-full
                                h-10
                                flex flex-row
                            "
                        >
                            <button
                                class="
                                    w-full
                                    h-full
                                    bg-black
                                    text-white
                                    font-sans font-medium
                                    text-base
                                    leading-6
                                    text-center
                                    flex flex-row
                                    justify-center
                                    items-center
                                    focus:outline-none
                                "
                                @click.prevent="submit"
                            >
                                <div
                                    class="
                                        not-italic
                                        font-sans font-medium
                                        text-base
                                        leading-6
                                        text-center text-white
                                    "
                                >
                                    {{
                                        this.$route.name === "RwLoginEmail"
                                            ? "Login"
                                            : "Sign Up"
                                    }}
                                </div>
                            </button>
                        </div>
                    </form>
                </div>
            </div>
            <v-alert
                v-if="error"
                type="error"
                class="mt-2"
                :value="error"
                :dismissible="false"
            >
                {{ errorMsg }}
            </v-alert>
            <v-alert
                v-if="success"
                type="success"
                class="mt-2"
                :value="success"
                :dismissible="false"
            >
                {{ successMsg }}
            </v-alert>
        </div>
        <!-- <div v-else>
            already logged in
        </div> -->
    </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
    name: "RwLoginRegisterEmail",
    data() {
        return {
            showPassword: false,
            accountInfo: {
                firstName: "",
                lastName: "",
                email: "",
                password: "",
            },
            error: false,
            errorMsg: "",
            success: false,
            successMsg: "",
        };
    },

    methods: {
        ...mapActions("user", ["userLogin", "addAccount", "userLogout"]),
        async submit() {
            console.log("submit");
            if (sessionStorage.getItem("api_token") === null) {
                try {
                    await this.userLogin({
                        application_id: "05d8ab7fc9884950b6f90cad61dc88aa",
                        login_type: 1,
                        login_id: this.accountInfo.email,
                        login_secret: this.accountInfo.password,
                        device_id: "device_id_1148",
                        action:
                            this.$route.name === "RwLoginEmail"
                                ? "login"
                                : "register",
                    });
                    if (this.$route.name !== "RwLoginEmail") {
                        await this.addAccount({
                            addAccount_target_id:
                                sessionStorage.getItem("user_id"),
                            addAccount_target_type: 3,
                        });
                    }
                    this.error = false;
                    this.success = true;
                    this.successMsg =
                        this.$route.name === "RwLoginEmail"
                            ? "Login Success, redirecting..."
                            : "Sign Up Success, redirecting...";
                    // redirect to home page after 3 seconds
                    setTimeout(() => {
                        if (sessionStorage.getItem("cart") !== null) {
                            this.$router.push("/cart");
                        } else {
                            this.$router.push("/plans");
                        }
                        this.$router.go();
                    }, 3000);
                } catch (error) {
                    this.userLogout();
                    this.error = true;
                    this.errorMsg = error;
                }
            } else {
                // already logged in
                console.log("already logged in");
            }
        },
    },
};
</script>