<script>
import { mapActions } from "vuex";
export default {
    name: "FTProduct",
    components: {},
    data() {
        return {
            item: [],
            payInFullChecked: true,
            afterpayChecked: false,
            snackbar: false,
            addedItem: null,
            loading: true,
            selectedStorage: "",
            quantity: 1,
        };
    },
    computed: {},
    methods: {
        // ...mapActions('cart', ['addToCart']),
        ...mapActions("checkout", ["setPlanInfo"]),
        // add to cart
        addToCart(item, quantity) {
            // debugger
            this.snackbar = true;
            this.addedItem = item.data.attributes.title;
            // console.log("@addToCart: ", item);
            // add item to session storage
            let cart = JSON.parse(sessionStorage.getItem("cart"));
            if (cart === null) {
                cart = [];
            }
            // if afterpay is checked, mark the item as afterpay
            if (this.afterpayChecked) {
                item.afterpay = true;
            } else {
                item.afterpay = false;
            }
            // if the item is already in the cart, just add 1 to the quantity, otherwise add the item to the cart and set the quantity to 1
            let index = cart.findIndex(
                (i) =>
                    i.data.id === item.data.id && i.afterpay === item.afterpay
            );
            if (index === -1) {
                item.quantity = quantity;
                cart.push(item);
            } else {
                cart[index].quantity += quantity;
            }

            // save to session storage
            sessionStorage.setItem("cart", JSON.stringify(cart));
            // console.log("cart: ", cart);
        },
        goToCart() {
            this.$router.push({
                name: "RwCart",
            });
        },
    },
    watch: {
        // only check one of afterpay or pay in full
        payInFullChecked(val) {
            if (val) {
                this.afterpayChecked = false;
            }
        },
        afterpayChecked(val) {
            if (val) {
                this.payInFullChecked = false;
            }
        },
    },
    async created() {
        await this.$axios
            .get(
                this.$strapi_url +
                    "stores/" +
                    this.$route.params.id +
                    "?populate=*"
            )
            .then((res) => {
                this.item = res.data;
                // console.log("this.item", this.item);
                this.loading = false;
            });
    },
};
</script>