<script>
import Decimal from "decimal.js";
import { mapActions } from "vuex";
export default {
    name: "FTCheckout",
    props: {},
    data() {
        return {
            cart: [],

            addressInfo: {
                firstName: "",
                lastName: "",
                name: "",
                street: "",
                suite: "",
                city: "",
                state: "",
                zip: "",
            },
        };
    },
    methods: {
        ...mapActions("checkout", ["setAddress"]),
        multiply(a, b) {
            return new Decimal(a).times(b).toNumber();
        },
        add(a, b) {
            return new Decimal(a).plus(b).toNumber();
        },
        removeItem(id) {
            this.cart = this.cart.filter((item) => item.data.id !== id);
            // update cart in session storage
            sessionStorage.setItem("cart", JSON.stringify(this.cart));
        },
        incrementQuantity(index) {
            this.cart[index].quantity += 1;
            // update cart in session storage
            sessionStorage.setItem("cart", JSON.stringify(this.cart));
        },
        decrementQuantity(index) {
            if (this.cart[index].quantity > 1) {
                this.cart[index].quantity -= 1;
                // update cart in session storage
                sessionStorage.setItem("cart", JSON.stringify(this.cart));
            }
        },
        async submitAddress() {
            // connect firstname and lastname
            this.addressInfo.name = `${this.addressInfo.firstName} ${this.addressInfo.lastName}`;
            // remove firstname and lastname
            delete this.addressInfo.firstName;
            delete this.addressInfo.lastName;
            console.log("@@@", this.addressInfo);
            await this.setAddress(this.addressInfo);
            this.$router.push({ name: "RwCheckoutShipping" });
        },
    },
    computed: {
        subtotal() {
            let total = 0;
            this.cart.forEach((item) => {
                total = this.add(
                    this.multiply(item.data.attributes.price, item.quantity),
                    total
                );
            });
            return total;
        },
        payInFull() {
            let total = 0;
            this.cart.forEach((item) => {
                if (!item.afterpay) {
                    total = this.add(
                        this.multiply(
                            item.data.attributes.price,
                            item.quantity
                        ),
                        total
                    );
                } else {
                    total = this.add(0, total);
                }
            });
            return total;
        },
        afterpay() {
            let total = 0;
            this.cart.forEach((item) => {
                if (item.afterpay) {
                    total = this.add(
                        this.multiply(
                            item.data.attributes.price / 4,
                            item.quantity
                        ),
                        total
                    );
                } else {
                    total = this.add(0, total);
                }
            });
            return total;
        },
    },
    mounted() {
        // get cart from session storage
        this.cart = JSON.parse(sessionStorage.getItem("cart"));
        // console.log the route
        // console.log(this.$route.fullPath);
        // if not logged in, redirect to login page
        if (sessionStorage.getItem("api_token") === null) {
            this.$router.push({ name: "RwLogin" });
        }
    },
};
</script>
