import axios from 'axios'

import nProgress from 'nprogress'
import 'nprogress/nprogress.css'

const requestsStrapi = axios.create({
    baseURL:
        process.env.NODE_ENV === 'production' ? 'https://demo-cms.lotusflare.com/api/' : 'http://localhost:1337/api/',
    // timeout: 10000,
    // withCredentials: true
})

requestsStrapi.interceptors.request.use((config) => {
    nProgress.start()
    // const api_token = sessionStorage.getItem('api_token')
    // if (api_token) {
    //     config.headers.Authorization = `Bearer ${api_token}`
    // }
    return config
})

requestsStrapi.interceptors.response.use(
    (response) => {
        nProgress.done()
        return response.data
    },
    (error) => {
        // if (error.response.status === 401) {
        //     localStorage.removeItem('api_token')
        //     window.location.reload()
        // }
        nProgress.done()
        return Promise.reject(error)
    }
)

export default requestsStrapi
