<template>
    <div>
        <SubHeader>
            <template #title
                >{{
                    currentParentCategory ? currentParentCategory : "Consumer"
                }}
                Plans</template
            >
            <template #subtitle>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                <br />In congue ante ac nunc bibendum volutpat. <br />
                Aenean iaculis sem utvelit lobortis.
            </template>
        </SubHeader>
        <v-container fluid class="mt-4 mb-12 max-w-7xl relative">
            <v-row class="gap-24">
                <v-col cols="auto">
                    <div
                        class="ml-4 font-semibold text-xl leading-7"
                        v-show="currentParentCategory !== 'Uncategorized'"
                    >
                        Filters
                    </div>
                    <v-sheet
                        elevation="0"
                        width="190px"
                        v-show="currentParentCategory !== 'Uncategorized'"
                        class="flex flex-col items-start p-0 gap-4 mt-4 ml-4"
                        color="bgColor"
                    >
                        <div class="text-sm font-bold">Categories</div>
                        <v-skeleton-loader
                            v-if="loading"
                            class="mx-auto"
                            width="220px"
                            type="image"
                        ></v-skeleton-loader>
                        <div
                            v-else
                            v-for="(item, index) in items"
                            :key="`item-${index}`"
                        >
                            <FTCheckbox v-model="selected" :label="item">
                                {{ item }}
                            </FTCheckbox>
                        </div>
                    </v-sheet>
                </v-col>

                <v-col>
                    <div>
                        <div class="flex justify-end mr-4 p-0">
                            <div>
                                <v-select
                                    v-model="selectedSort"
                                    :items="sortOptions"
                                    label="Sort by"
                                    outlined
                                    dense
                                    class="w-48"
                                ></v-select>
                            </div>
                        </div>
                        <div v-if="filteredOffers" class="text-end mr-4 mb-4">
                            Showing {{ filteredOffers.length }} Products
                        </div>
                        <div v-else class="text-end mr-4 mb-4">Loading...</div>

                        <div class="grid grid-cols-3 gap-5 pistion-relative">
                            <template v-for="(item, index) in filteredOffers">
                                <FTCard
                                    :title="item.attributes.title"
                                    :image="
                                        $upload_url +
                                        item.attributes.image.data?.attributes
                                            ?.url
                                    "
                                    :description="item.attributes.body"
                                    :price="item.attributes.price"
                                    :link="'/plans/' + item.id"
                                    :key="`offers-${index}`"
                                />
                            </template>
                        </div>
                    </div>
                </v-col>
            </v-row>
            <v-snackbar v-model="snackbar" timeout="2000">
                Added {{ addedItem }} to cart
                <template #action="{ attrs }">
                    <!-- button redirect to cart -->
                    <v-btn
                        color="primary"
                        text
                        v-bind="attrs"
                        @click="goToCart"
                    >
                        Go to cart
                    </v-btn>
                </template>
            </v-snackbar>
        </v-container>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import FTCard from "@/components/FTCard.vue";
// import FTCategoriesFilter from "@/components/FTCategoriesFilter.vue";
import FTCheckbox from "@/components/FTCheckbox.vue";
import SubHeader from "@/components/SubHeader.vue";
// import axios from "axios";
export default {
    name: "NewPlans",
    components: {
        FTCard,
        // FTCategoriesFilter,
        FTCheckbox,
        SubHeader,
    },
    data() {
        return {
            items: [],
            stores: [],
            // plans: [],
            // toggle_exclusive: 0,
            selected: {
                checked: [],
                disabled: [],
            },
            snackbar: false,
            addedItem: null,
            itemLimit: 3,
            sortOptions: ["Title", "Price: Low to High", "Price: High to Low"],
            selectedSort: "Title",
        };
    },
    computed: {
        // get plans/offers from store
        ...mapState({
            offers: (state) => state.home.offers,
            filteredOffers: (state) => state.home.filteredOffers,
            parentCategories: (state) => state.home.parentCategories,
            subCategories: (state) => state.home.subCategories,
            currentParentCategory: (state) => state.home.currentParentCategory,
            loading: (state) => state.home.loading,
        }),
    },
    methods: {
        ...mapActions("home", [
            "getOffers",
            "getParentCategories",
            "getSubCategories",
            "getFilteredOffers",
            "setSort",
        ]),
        filterCheckbox() {
            this.getFilteredOffers(this.selected.checked);
        },
        goToCart() {
            this.$router.push({
                name: "RwCart",
            });
        },
    },
    watch: {
        // selected.checked is the category that the user selects. Here, we watch the change of selected.checked to implement the linkage of checkbox and update selected.checked
        "selected.checked": {
            handler() {
                this.filterCheckbox();
            },
        },
        // 监视subCategories的变化。当用户变更父category时，subCategories会变化。
        subCategories(newValue) {
            this.selected.checked = [...Object.values(newValue), "Other"];
            this.items = this.selected.checked;
            this.filterCheckbox();
        },
        // watch sortOptions change
        selectedSort(newValue) {
            console.log(newValue);
            if (newValue === "Price: Low to High") {
                this.setSort("price:asc");
            } else if (newValue === "Price: High to Low") {
                this.setSort("price:desc");
            } else if (newValue === "Title") {
                this.setSort("title:asc");
            }
            this.filterCheckbox();
            // this.setSort(newValue);
        },
    },
    async mounted() {
        // await this.getOffers();
        await this.filterCheckbox();
        // console.log("this.parentCategories: ", this.parentCategories);
        // console.log("this.subCategories: ", this.subCategories);
        await this.getParentCategories();
        if (this.currentParentCategory) {
            await this.getSubCategories(this.currentParentCategory);
        } else {
            await this.getSubCategories(this.parentCategories[0]);
        }
        // console.log("this.subCategories: ", this.subCategories);
    },
    // beforeMount() {
    //     axios.get(this.$strapi_url + "import").then((res) => {
    //         console.log(res);
    //     });
    // },
};
</script>
